import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { ChallengesTasks } from './challenges-tasks.component';
export const routes = [
  {path: '', component: ChallengesTasks, pathMatch: 'full'}
];

@NgModule({
  declarations: [
    // Components / Directives/ Pipes
    ChallengesTasks
  ],
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild(routes),
  ]
})
export default class ChallengesTasksModule {
  static routes = routes;
}



